export const formatDate = (date) => {
  let dd = date.getDate()
  if (dd < 10) dd = '0' + dd

  let mm = date.getMonth() + 1
  if (mm < 10) mm = '0' + mm

  let yy = date.getFullYear() % 100
  if (yy < 10) yy = '0' + yy

  return dd + '.' + mm + '.' + yy
}

export const formatErrorMessage = (message) => {
  switch (message) {
    case 'Firebase: Error (auth/email-already-in-use).':
      return '–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Å —Ç–∞–∫–∏–º email —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç!'

    case 'Firebase: Error (auth/invalid-email).':
      return '–ù–µ–ø—Ä–∞–≤–∏–ª—å–Ω—ã–π email!'

    case 'Firebase: Error (auth/internal-error).':
      return '–í—Å–µ –ø–æ–ª—è –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã!'

    case 'Firebase: Error (auth/user-not-found).':
      return '–¢–∞–∫–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–µ —Å—É—â–µ—Å—Ç–≤–≤—É–µ—Ç!'

    case 'Firebase: Error (auth/wrong-password).':
      return '–ù–µ–ø—Ä–∞–≤–∏–ª—å–Ω—ã–π –ø–∞—Ä–æ–ª—å!'

    default:
      break
  }
}
